<template>
  <v-container>
    <v-form
        @submit.prevent
    >
      <v-row
          justify="space-around"
      >
        <v-col
            cols="12"
        >
          <v-row>
            <v-col
              cols="auto"
            >
              {{ countProducts }} Products edit.
              <v-switch
                  class="hidden"
                  style="display:none;"
                  v-model="allProducts"
              >
                <template v-slot:label>
                  <v-row
                    no-gutters
                  >
                    <v-col
                        v-if="countProducts"
                        cols="auto"
                        class="my-5 mx-1"
                        :class="classCountProducts"
                    >
                      {{ countProducts }}
                    </v-col>
                    <v-col
                        v-if="countProducts"
                        cols="auto"
                        class="my-5 mx-1"
                    >
                      /
                    </v-col>
                    <v-col
                        cols="auto"
                        class="my-5 mx-1"
                        :class="classCountAll"
                    >
                      {{ textEditAll }}
                    </v-col>
                  </v-row>
                </template>
              </v-switch>
            </v-col>
          </v-row>
        </v-col>
        <v-col
            cols="12"
        >
          <v-text-field
              v-model="search"
              label="What search?"
          ></v-text-field>
          <v-text-field
              v-model="replace"
              label="How to replace?"
          ></v-text-field>
        </v-col>
        <v-col
            cols="12"
        >
          <v-data-table
              :headers="headers"
              :items="desserts"
              class="elevation-1"
              hide-default-footer
          >
            <template v-slot:top>
              <v-toolbar
                  flat
              >
                Where to look?
              </v-toolbar>
            </template>
            <template v-slot:item.action="{ item }">
              <v-icon
                  v-if="false"
                  small
                  class="mr-2"
                  @click="editItem(item)"
                  aria-hidden="false"
              >
                &nbsp;mdi-pencil&nbsp;
              </v-icon>
              <v-icon
                  small
                  @click="deleteItem(item)"
                  aria-hidden="false"
              >
                &nbsp;mdi-delete&nbsp;
              </v-icon>
            </template>
            <template v-slot:no-data>Click "New Item" and create new task!</template>
            <template v-slot:footer>
              <v-toolbar
                  flat
              >
                <v-spacer></v-spacer>
                <v-dialog
                    v-model="dialog"
                    max-width="500px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn
                        color="primary"
                        dark
                        class="mb-2"
                        v-bind="attrs"
                        v-on="on"
                    >
                      {{ editBtnText }}
                    </v-btn>
                  </template>
                  <v-card>
                    <v-card-title>
                      <span class="text-h5">{{ formTitle }}</span>
                    </v-card-title>

                    <v-card-text>
                      <v-container>
                        <v-row>
                          <v-col
                              cols="12"
                              md="6"
                          >
                            <v-select
                                v-model="selectedField"
                                :items="fields"
                                return-object
                                label="Select field:"
                            ></v-select>
                          </v-col>
                          <v-col
                              cols="12"
                              md="6"
                          >
                            <v-select
                                v-model="selectedLanguage"
                                :items="languages"
                                item-text="name"
                                return-object
                                label="Select language:"
                            ></v-select>
                          </v-col>
                        </v-row>
                      </v-container>
                    </v-card-text>

                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn
                          color="blue darken-1"
                          text
                          @click="close"
                      >
                        Cancel
                      </v-btn>
                      <v-btn
                          color="blue darken-1"
                          text
                          @click="save"
                      >
                        OK
                      </v-btn>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
                <v-dialog v-model="dialogDelete" max-width="500px">
                  <v-card>
                    <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
                      <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                      <v-spacer></v-spacer>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
              </v-toolbar>
            </template>
          </v-data-table>
        </v-col>
        <v-col
          cols="12"
        >
          <v-btn
              color="primary"
              dark
              class="mb-2"
              @click="submit"
          >
            Post
          </v-btn>
        </v-col>
      </v-row>
    </v-form>
  </v-container>
</template>

<script>
export default {
  name: 'Parser-Table',
  props: {
    base: String,
    token: String
  },
  data: () => ({
    appPath: 'extension/module/multireplacer',
    dialog: false,
    dialogDelete: false,
    headers: [
      {text: 'Field', align: 'center', sortable: false, value: 'text'},
      {text: 'Language', align: 'center', sortable: false, value: 'name'},
      {text: 'Actions', align: 'center', sortable: false, value: 'action'}
    ],
    search: '',
    replace: '',
    languages: [],
    fields: [],
    productString: '',
    desserts: [],
    editedIndex: -1,
    deleteTaskId: -1,
    selectedField: {},
    selectedLanguage: {},
    // TODO: изменить после получения данных с сервера
    defaultField: {
      text: '',
      value: ''
    },
    defaultLanguage: {
      language_id: '',
      name: '',
      code: '',
      locale: '',
      image: '',
      directory: '',
      sort_order: '0',
      status: '1'
    },
    allProducts: true,
    textEditAll: 'All products'
  }),

  computed: {
    isEdited() {
      return (this.selectedField?.value?.length || this.selectedLanguage?.code?.length)
    },
    editBtnText() {
      return this.isEdited ? 'Add Item' : 'New Item'
    },
    formTitle() {
      return this.editedIndex === -1 ? 'New Task for search' : 'Edit Item'
    },
    classCountProducts() {
      return this.allProducts ? '' : 'primary--text'
    },
    countProducts() {
      return this?.productString?.replaceAll(/\s+/g,'').split(',').length || 0
    },
    classCountAll() {
      return this.allProducts ? 'primary--text' : ''
    }
  },

  watch: {
    dialog(val) {
      val || this.close()
    },
    dialogDelete(val) {
      val || this.closeDelete()
    },
  },

  // получить исходные данные
  mounted() {
    this.initialize()
  },
  beforeUpdate() {
    console.log('data update')
  },

  methods: {
    initialize() {
      this.getData()
      this.getLocal()
    },
    getData() {
      let self = this

      this.axios.get(`${this.base}index.php?route=${this.appPath}/loaddata&${this.token}`)
          .then(function (response) {
            // handle success
            console.log(response);

            // обновляем данные
            self.fields = response.data.fields
            self.languages = Object.values(response.data.languages)

            self.productString = response.data.products

            // для возможности передачи данных на старте из контроллера
            self.desserts = response.data.tasks
          })
          .catch(function (error) {
            // handle error
            console.log(error);
          })
          .then(function () {
            // always executed
            console.log('end get');
          });
    },
    getLocal() {
      // let self = this

      this.axios.get(`${this.base}index.php?route=${this.appPath}/loadlocal&${this.token}`)
          .then(function (response) {
            // handle success
            console.log(response);

            // обновляем данные
            // self.fields = response.data.fields
            // self.languages = Object.values(response.data.languages)

            // для возможности передачи данных на старте из контроллера
            // self.desserts = response.data.tasks
          })
          .catch(function (error) {
            // handle error
            console.log(error);
          })
          .then(function () {
            // always executed
            console.log('end get');
          });
    },

    // editItem (item) {
    //   this.editedIndex = this.desserts.indexOf(item)
    //   this.selectedField = {
    //     text: item.text,
    //     value: item.value
    //   },
    //   this.selectedLanguage = {
    //     name: item.name,
    //     code: item.code
    //   },
    //   this.dialog = true
    // },
    deleteItem(item) {
      this.editedIndex = this.desserts.indexOf(item)

      // на всякий случай, запоминаем данные из удалённого эллемента
      this.selectedField = {
        text: item.text,
        value: item.value
      },
          this.selectedLanguage = {
            name: item.name,
            code: item.code
          },
          this.dialogDelete = true
    },
    deleteItemConfirm() {
      if (this.editedIndex > -1)
        this.desserts.splice(this.editedIndex, 1)

      console.log('delete-splice id' + this.editedIndex)

      this.deleteTaskId = -1
      this.closeDelete()
    },
    close() {
      this.dialog = false
      this.$nextTick(() => {
        this.selectedField = Object.assign({}, this.defaultField)
        this.selectedLanguage = Object.assign({}, this.defaultLanguage)
        this.editedIndex = -1
      })
    },
    closeDelete() {
      this.dialogDelete = false
      this.$nextTick(() => {
        // this.selectedField = Object.assign({}, this.defaultField) // ?
        // this.selectedLanguage = Object.assign({}, this.defaultLanguage) // ?
        this.editedIndex = -1
      })
    },
    save() {
      // if (this.editedIndex > -1) {
      //   this.desserts[this.editedIndex] = Object.assign(this.selectedField, this.selectedLanguage)
      // } else {
        let newTask = Object.assign({}, this.selectedField, this.selectedLanguage)
        this.desserts.push(newTask)
      // }
      this.close()
    },
    submit() {
      // this.axios.defaults.headers.common['Accept'] = 'Accept: application/json, text/javascript, */*; q=0.01';
      // this.axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded; charset=UTF-8';
      let self = this

      this.axios({
        method: 'post',
        url: `${this.base}index.php?route=${this.appPath}/postdata&${this.token}`,
        responseType: 'json',
        // headers: {'X-Requested-With': 'XMLHttpRequest'},
        data: {
          'search': self.search,
          'replace': self.replace,
          'tasks': self.desserts
        }
      })
      .then(function (response) {
        // handle success
        console.log(response);

        alert("Ok!");
      })
      .catch(function (error) {
        // handle error
        console.log(error);

        alert("Error!");
      })
      .then(function () {
        // always executed
        console.log('end get');
      });
    }
  }
}
</script>