<template>
  <v-app>
    <v-main>
      <ParserTable :base="base" :token="token"></ParserTable>
    </v-main>
  </v-app>
</template>

<script>
import ParserTable from './components/ParserTable';
// import { URL } from 'node:url';

// eslint-disable-next-line no-unused-vars
let base = ''
// eslint-disable-next-line no-unused-vars
let token = ''

let elmData = document.getElementById('data')
// let dataset_extension_page = base + 'index.php?route=extension/extension'
// let extension_page = ''

if (process.env.NODE_ENV !== 'production') {
  base = 'http://multiedit-pro/upload/admin/'
  console.log('base=' + base)
  // token = '' // &session_id=9ce4a1a63a95f5079b49e48316&user_token=VR4snM18fakaK5XaLK66FtNR6A3CWCjR
  // token = location.href.replace(/.{0,}(&token=.*|&user_token=.*)/gi, '$1').replace(/#.+/gi, '')
  // console.log('location.href=', location.href)

  let url = new URL(location.href)
  console.log('url=', url)

  // let user_token = url.searchParams.get('user_token')
  // console.log('user_token=', user_token)
  //
  // let session_id = url.searchParams.get('session_id')
  // console.log('session_id=', session_id)

  if (url.pathname !== 'undefined')
    token += url.pathname.replace(/^\/+/, '')

  if (url.search !== 'undefined')
    token += url.search.replace(/^\?+/, '')

  console.log(token)

  // ... || location.origin + '/upload/admin/' // TODO ?
} else {
  let data_http_server = elmData.dataset.httpserver // console.log('data_http_server=' . data_http_server)

  base = data_http_server || 'localhost/'
  console.log('base=', base)
  // console.log('location.origin=', location.origin)

  token = location.href.replace(/.{0,}(&token=.*|&user_token=.*)/gi, '$1').replace(/#.+/gi, '') // TODO new URL()
  console.log('token=', token)

  // dataset_extension_page = elmData.dataset.extensionpage
  // console.log(dataset_extension_page)
  // extension_page = dataset_extension_page || base + 'index.php?route=extension/extension'
  // console.log(extension_page)
}

export default {
  name: 'App',

  components: {
    ParserTable
  },

  data: () => ({
    base,
    token
  }),
};
</script>
